.rs-range {
	width: 80%;
	-webkit-appearance: none;
}
.rs-range:focus {
	outline: none;
}
.rs-range::-webkit-slider-runnable-track {
	width: 100%;
	height: 2px;
	cursor: pointer;
	box-shadow: none;
	background: #ffffff;
	border-radius: 0px;
	border: 0px solid #010101;
}
.rs-range::-moz-range-track {
	width: 100%;
	height: 2px;
	cursor: pointer;
	box-shadow: none;
	background: #ffffff;
	border-radius: 0px;
	border: 0px solid #010101;
}

.rs-range::-webkit-slider-thumb {
	box-shadow: none;
	border: 0px solid #ffffff;
	box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.25);
	height: 16px;
	width: 5px;
	border-radius: 22px;
	background: rgba(255, 255, 255, 1);
	cursor: pointer;
	-webkit-appearance: none;
	margin-top: -8px;
}
.rs-range::-moz-range-thumb {
	box-shadow: none;
	border: 0px solid #ffffff;
	box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.25);
	height: 16px;
	width: 5px;
	border-radius: 22px;
	background: rgba(255, 255, 255, 1);
	cursor: pointer;
	-webkit-appearance: none;
	margin-top: -8px;
}
.rs-range::-moz-focus-outer {
	border: 0;
}

.sample-inputs-container {
	padding-bottom: 4px;
	display: block;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: flex-end;
}
.sample-inputs-container h4 {
	padding-left: 10px;
	display: inline-block;
}

.sample-inputs-content {
	display: flex;
	justify-content: space-evenly;
}
.sample-input {
	display: inline-block;
}

.sample-input label {
	margin-top: 5px;
	padding: 0;
	color: whitesmoke;
	font-size: 12px;
	display: block;
	min-width: 120px;
	text-align: center;
}
